import doStringMath from './doStringMath';

export default function resetStates(state, settings) {
  state = {...state}
  const update = {};
  for (const key in state) {
    if (!key.startsWith('ui_')) continue;
    const fromKey = key.slice(3)
    update[key] = settings[fromKey]
    update[fromKey] = settings[fromKey]
  }
  const merged = {}
  for (const key in update) {
    // do math only on non ui values
    try {
      update[key] = key.startsWith('ui_') ? update[key] : doStringMath(update[key])
    } catch (err) {
      // Ignore mid-math errors
      delete update[key]
    }
  }
  for (const key in update) {
    const fromKey = key.startsWith('ui_') ? key.slice(3) : key;
    // if updated value matches settings then remove
    if (update[key] === settings[fromKey]) {
      update[key] = null;
      state[key] = null;
      merged[key] = null;
    }
  }
  for (const key in state) {
    const fromKey = key.startsWith('ui_') ? key.slice(3) : key;
    // output final value filling in defaults
    merged[key] = update[key] ?? state[key] ?? settings[fromKey]
  }
  return merged
}
