import './App.css';
import { langs } from './lang/langs';
import React, { useEffect } from 'react';
import {
  HashRouter as Router,
  Switch,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";
import { allSettingsChanged } from './store/settings';
import clsx from 'clsx'
import { useDispatch, useSelector } from 'react-redux';
import { InsulinUnits } from './InsulinUnits';
import { ProportionateCarbs } from './ProportionateCarbs';
import { Settings } from './Settings'
import { Warning } from './Warning'
import { About } from './About'
import { AppBar, List, IconButton, Toolbar, Divider, ListItem, ListItemIcon, ListItemText, Drawer, Typography } from '@material-ui/core'
import {
  ChevronLeft as ChevronLeftIcon,
  ChevronRight as ChevronRightIcon,
  Menu as MenuIcon,
  LocalHospitalOutlined as FoodInsulinIcon,
  Settings as SettingsIcon,
  SystemUpdate as SystemUpdateIcon,
  Info as WarningIcon,
  Help as AboutIcon,
  Speed as ProportionateCarbsIcon
} from '@material-ui/icons'
import { makeStyles, useTheme } from '@material-ui/core/styles'
const drawerWidth = 'auto';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer - 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  menuButton: {
    marginLeft: -20,
    marginRight: 20,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    width: theme.spacing(0) + 1,
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}));



function ScrollToTop({ history }) {
  useEffect(() => {
    const unlisten = history.listen(() => {
      window.scrollTo(0, 0);
    });
    return () => {
      unlisten();
    }
  }, []);

  return (null);
}

const ScrollToTopConnected = withRouter(ScrollToTop);

function App() {
  const dispatch = useDispatch();
  const view = useSelector(state => state.view)
  const settings = useSelector(state => state.settings)
  const lang = langs[settings.lang]
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  useEffect(() => {
    (async () => {
      const response = await fetch('./manifest.json');
      const manifest = await response.json();
      console.log(`Installed: ${settings.version}, Manifest: ${manifest.version}`)
      dispatch(allSettingsChanged({ needsUpdating: (manifest.version !== settings.version) }))
    })();
  })

  return (
    <Router>
      <ScrollToTopConnected />
      <div className="App">
        <AppBar
          position="fixed"
          className={clsx(classes.appBar, {
            [classes.appBarShift]: open,
          })}
        >
          <Toolbar>

            <Typography variant="h6" noWrap>
              <Route path="/insulinunits"><FoodInsulinIcon /></Route>
              <Route path="/proportionatecarbs"><ProportionateCarbsIcon /></Route>
              <Route path="/settings"><SettingsIcon /></Route>
              <Route path="/warning"><WarningIcon /></Route>
              <Route path="/about"><AboutIcon /></Route>
              &nbsp;{view.title}
            </Typography>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              edge="start"
              className={clsx(classes.menuButton, {
                [classes.hide]: open,
              })}
            >
              <MenuIcon />
            </IconButton>
          </Toolbar>
        </AppBar>
        <Drawer
          variant="permanent"
          className={clsx(classes.drawer, {
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          })}
          classes={{
            paper: clsx({
              [classes.drawerOpen]: open,
              [classes.drawerClose]: !open,
            }),
          }}
        >
          <div className={classes.toolbar}>
            <IconButton onClick={handleDrawerClose}>
              {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
            </IconButton>
          </div>
          <Divider />
          <List>
            <ListItem button key='Insulin' component={Link} to="/insulinunits" onClick={handleDrawerClose}>
              <ListItemIcon><FoodInsulinIcon /></ListItemIcon>
              <ListItemText primary={lang.insulinUnitsTitle} />
            </ListItem>
            <ListItem button key='Carbs' component={Link} to='/proportionatecarbs' onClick={handleDrawerClose}>
              <ListItemIcon><ProportionateCarbsIcon /></ListItemIcon>
              <ListItemText primary={lang.proportionateCarbsTitle} />
            </ListItem>
          </List>
          <Divider />
          <List>
            <ListItem button key='Settings' component={Link} to='/settings' onClick={handleDrawerClose}>
                <ListItemIcon><SettingsIcon /></ListItemIcon>
                <ListItemText primary={'Settings'} />
            </ListItem>
            <ListItem button key='Warning' component={Link} to='/warning' onClick={handleDrawerClose}>
                <ListItemIcon><WarningIcon /></ListItemIcon>
                <ListItemText primary={'Warning'} />
            </ListItem>
            <ListItem button disabled={!settings.needsUpdating} key='Update' onClick={() => {
              window.serviceWorkerRegistration.active.postMessage('update')
            }}>
                <ListItemIcon><SystemUpdateIcon /></ListItemIcon>
                <ListItemText primary={'Update'} />
            </ListItem>
            <ListItem button key='About' component={Link} to='/about' onClick={handleDrawerClose}>
                <ListItemIcon><AboutIcon /></ListItemIcon>
                <ListItemText primary={'About'} />
            </ListItem>
          </List>
        </Drawer>
        <main className={classes.content}>
          <div className={classes.toolbar} />
          <Switch>
            <Route path="/insulinunits">
              <InsulinUnits />
            </Route>
            <Route path="/proportionatecarbs">
              <ProportionateCarbs />
            </Route>
            <Route path="/settings">
              <Settings />
            </Route>
            <Route path="/warning">
              <Warning />
            </Route>
            <Route path="/about">
              <About />
            </Route>
            <Redirect to="/insulinunits" />
          </Switch>
        </main>
      </div>
    </Router>
  );
}

export default App;
