import mergeStates from "./mergeStates"

export default function makeOnChanged(state, settings, dispatch, action, { noui = false } = {}) {
  return (name, event) => {
    const value = event.target.value
    try {
      dispatch(
        action(mergeStates(
          state,
          settings,
          noui
          ? {
            [name]: value || settings[name]
          }
          : {
            [name]: value || settings[name],
            [`ui_${name}`]: value
          }
        ))
      )
    } catch (err) {}
  }
}
