import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import {
  Link
} from "react-router-dom";
import { Typography, TextField, InputAdornment, Button, Select, InputLabel } from '@material-ui/core'
import Alert from '@material-ui/lab/Alert';
import { titleChanged } from './store/view';
import { allInsulinUnitsChanged } from './store/insulinUnits';
import { allProportionateCarbsChanged } from './store/proportionateCarbs'
import mergeStates from './utils/mergeStates'
import makeOnFocusSelect from './utils/makeOnFocusSelect';
import makeOnChanged from './utils/makeOnChanged';
import makeOnBlur from './utils/makeOnBlur'
import {
  outputContainer,
  topButton
} from './utils/styles';
import {
  Maximize as MaximizeIcon,
  MenuOpen as MenuOpenIcon,
  RadioButtonUnchecked as RadioButtonUncheckedIcon,
  Cake as CakeIcon,
  DataUsage as DataUsageIcon,
  Add as AddIcon,
  Autorenew as AutorenewIcon,
  Speed as ProportionateCarbsButtonIcon,
  Info as InfoIcon,
} from '@material-ui/icons'
import resetStates from './utils/resetStates';
import { langs } from './lang/langs';

export const ProportionateCarbs = () => {
  const insulinUnitsState = useSelector(state => state.insulinUnits)
  const state = useSelector(state => state.proportionateCarbs)
  const settings = useSelector(state => state.settings)
  const lang = langs[settings.lang]
  const values = mergeStates(state, settings)
  const dispatch = useDispatch()

  const onKeyUp = (event) => {
    if (event.keyCode !== 13) return;
    event.target.blur()
  }
  const onFocusSelect = makeOnFocusSelect()
  const onChanged = makeOnChanged(state, settings, dispatch, allProportionateCarbsChanged)
  const onBlur = makeOnBlur(settings, dispatch, allProportionateCarbsChanged)

  useEffect(() => {
    dispatch(
      titleChanged(lang.proportionateCarbsTitle)
    )
  })

  const [showCarbsPerWeight, setShowCarbsPerWeight] = useState(false)
  const [showColumnWeight, setShowColumnWeight] = useState(false)
  const [showTotalWeight, setShowTotalWeight] = useState(false)
  const [showPortionRatio, setShowPortionRatio] = useState(false)

  return (
    <section className="proportionatecarbs screen__container">

      <div className="proportionatecarbs__output" style={outputContainer}>
        <Typography variant="h2" noWrap>
          {state.roundedConsumedCarbs||0}g
        </Typography>
      </div>

      <div className="proportionatecarbs__button__reset">
        <Button
          className='button'
          style={topButton}
          onClick={
            () => {
              dispatch(allProportionateCarbsChanged(resetStates(
                state,
                settings
              )))
            }
          }
        >
          <AutorenewIcon />
          &nbsp;Reset
        </Button>
      </div>

      <Typography className="section__divider" variant="h6" noWrap>
        {lang.proportionateCarbsCarbRatioTitle}
      </Typography>

      <div className="proportionatecarbs_input input__group">

        <div className="proportionatecarbs__input__carbsperweight__container">
          <div className="proportionatecarbs__input__carbspercolumnweight input__container">
            <TextField label={<div>
            {lang.proportionateCarbsCarbsPerWeightLabelText}
            {lang.proportionateCarbsCarbsPerWeightInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowCarbsPerWeight(!showCarbsPerWeight)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showCarbsPerWeight ? 'flex':'none'}} onClose={() => setShowCarbsPerWeight(false)}>
                {lang.proportionateCarbsCarbsPerWeightInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
              onKeyUp={onKeyUp}
              onFocus={onFocusSelect.bind(this, 'carbsPerColumnWeight')}
              onChange={onChanged.bind(this, 'carbsPerColumnWeight')}
              onBlur={onBlur.bind(this, 'carbsPerColumnWeight')}
              value={values.ui_carbsPerColumnWeight}
              InputProps={{
                startAdornment: <InputAdornment position="start"><MenuOpenIcon /></InputAdornment>,
                endAdornment: <InputAdornment position="end">g</InputAdornment>,
              }}
            />
          </div>

          <div className="proportionatecarbs__input__columnweight input__container">
            <TextField label={<div>
            {lang.proportionateCarbsColumnWeightLabelText}
            {lang.proportionateCarbsColumnWeightInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowColumnWeight(!showColumnWeight)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showColumnWeight ? 'flex':'none'}} onClose={() => setShowColumnWeight(false)}>
                {lang.proportionateCarbsColumnWeightInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
              onKeyUp={onKeyUp}
              onFocus={onFocusSelect.bind(this, 'columnWeight')}
              onChange={onChanged.bind(this, 'columnWeight')}
              onBlur={onBlur.bind(this, 'columnWeight')}
              value={values.ui_columnWeight}
              InputProps={{
                startAdornment: <InputAdornment position="start"><MaximizeIcon /></InputAdornment>,
                endAdornment: <InputAdornment position="end">g</InputAdornment>,
              }}
            />
          </div>
        </div>

        <Typography className="section__divider" variant="h6" noWrap>
          {lang.proportionateCarbsAmountConsumedTitle}
        </Typography>

        <div className="proportionatecarbs__input__amountconsumed__container">
        <div className="proportionatecarbs__input__totalpacketweight input__container">
          <TextField label={<div>
            {lang.proportionateCarbsTotalWeightLabelText}
            {lang.proportionateCarbsTotalWeightInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowTotalWeight(!showTotalWeight)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showTotalWeight ? 'flex':'none'}} onClose={() => setShowTotalWeight(false)}>
                {lang.proportionateCarbsTotalWeightInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            onKeyUp={onKeyUp}
            onFocus={onFocusSelect.bind(this, 'totalPacketWeight')}
            onChange={onChanged.bind(this, 'totalPacketWeight')}
            onBlur={onBlur.bind(this, 'totalPacketWeight')}
            value={values.ui_totalPacketWeight}
            InputProps={{
              startAdornment: <InputAdornment position="start"><CakeIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
          />
        </div>
        <div className="proportionatecarbs__input__portionratio input__container">
          <InputLabel htmlFor="portionratio">{lang.proportionateCarbsPortionRatioLabelText}</InputLabel>
          <div className="select__container">
          <Select
            native
            value={values.ui_portionRatio}
            onChange={onChanged.bind(this, 'portionRatio')}
            onBlur={onBlur.bind(this, 'portionRatio')}
            label={<div>
            {lang.proportionateCarbsPortionRatioLabelText}
            {lang.proportionateCarbsPortionRatioInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowPortionRatio(!showPortionRatio)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showPortionRatio ? 'flex':'none'}} onClose={() => setShowPortionRatio(false)}>
                {lang.proportionateCarbsPortionRatioInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            inputProps={{
              name: 'portionratio',
              id: 'portionratio',
            }}
            startAdornment={<InputAdornment position="start"><DataUsageIcon /></InputAdornment>}
          >
            <option aria-label="None" value={1}>Whole</option>
            <option value={1/2}>1/2 Half</option>
            <option value={1/3}>1/3 One third</option>
            <option value={1/4}>1/4 One quarter</option>
            <option value={1/5}>1/5 One fifth</option>
            <option value={1/6}>1/6 One sixth</option>
            <option value={1/8}>1/8 One eighth</option>
            <option value={2}>2 Two</option>
            <option value={2/3}>2/3 Two thirds</option>
            <option value={2/5}>2/5 Two fifths</option>
            <option value={3}>3 Three</option>
            <option value={3/5}>3/5 Three fifths</option>
            <option value={3/4}>3/4 Three quarters</option>
            <option value={4}>4 Four</option>
            <option value={4/5}>4/5 Four fifths</option>
            <option value={5}>5 Five</option>
            <option value={5/6}>5/6 Five sixths</option>
          </Select>
          </div>
        </div>
        </div>
      </div>

      <div className="proportionatecarbs__button__addtoinsulin">
        <Button
          className='button'
          style={topButton}
          component={Link} to="/insulinunits"
          onClick={
            () => {
              const carbs = (state.roundedConsumedCarbs||0);
              if (!carbs) return;
              const value = parseFloat(insulinUnitsState.ui_consumedCarbs)
                ? `${insulinUnitsState.ui_consumedCarbs}+${carbs}`
                : `${carbs}`
              dispatch(allInsulinUnitsChanged(mergeStates(
                insulinUnitsState,
                settings,
                {
                  'consumedCarbs': value,
                  'ui_consumedCarbs': value
                }
              )))
            }
          }
        >
          <AddIcon />
          &nbsp;{lang.proportionateCarbsAddButtonText}
        </Button>
      </div>

    </section>
  )
}
