export default function makeOnBlur(defaults, dispatch, action, { noui = false } = {}) {
  return (name, event) => {
    const value = event.target.value
    if (value !== "") return;
    dispatch(
      action(
        noui
        ? {
          [name]: defaults[name],
        }
        : {
          [name]: defaults[name],
          [`ui_${name}`]: defaults[name]
        }
      )
    )
  }
}
