import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux'
import { titleChanged } from './store/view';
import { Typography } from '@material-ui/core';

export const Warning = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(
      titleChanged('Warning')
    )
  })

  return <section className="proportionatecarbs screen__container">
    <Typography variant="h4" noWrap>
      WARNING:
    </Typography>
    <div>
    This app is to be used only as an <b>indication</b> of dosage amounts.
    <p>
      It is imperative that the user understands how to use the app and accepts liability for its use. The author accepts no liability for loss or damage resulting from the use of this app.
    </p>
    <p>
      Please make sure you understand, set and appropriately adjust your "<b>Default blood glucose correction per unit</b>" and "<b>Default carbs per unit</b>" amounts in the settings panel before using this app.
    </p>
    <p>
      The bolus insulin calculator <b>DOES NOT</b> take into account rising and falling of your blood glucose over time. You <b>MUST</b> adjust your units manually.
    </p>
    <p>
      It is advised to start with the correct <b>BASAL</b> insulin first. To do this you should eat a no carb evening meal or fast on your evening meal. Adjust you basal insulin until your blood glucose is flat over night. Make sure to do this when you are not ill, stressed or doing abnormal amounts of exercise, you need a baseline basal that you can adjust later.
    </p>
    <p>
      Once your basal insulin dose is correct, you can change you default carbohydrates per unit and default blood glucose correction per unit to best suit meal times and corrections. A bigger carbs per unit and blood glucose per unit value will mean less insulin, a smaller value will mean more insulin.
    </p>
    </div>
  </section>

}
