export default function makeOnFocusGotoEndOrSelectZero(defaults, dispatch, action, { noui = false } = {}) {
  return (name, event) => {
    const value = event.target.value
    if (value === "0") {
      event.target.select();
      return
    }
    if (event.target.setSelectionRange) {
      var len = event.target.value.length * 2;
      setTimeout(() => {
        event.target.setSelectionRange(len, len);
      }, 0)
    } else {
      event.target.value = ''
      event.target.value = value
    }
    event.target.scrollTop = 99999
  }
}
