function addUI(settings) {
  if (!settings) return settings;
  const output = {};
  for (const key in settings) {
    if (key.startsWith('ui_')) continue;
    output[key] = settings[key]
    output[`ui_${key}`] = settings[key]
  }
  return output
}

function removeVersion(settings) {
  return {...settings, version: null}
}

export const defaultSettings = addUI({
  version: "0.0.12",
  needsUpdating: false,
  consumedCarbs: 0,
  currentBg: 5,
  minimumUnits: 0.1,
  carbsPerUnit: 0,
  bgPerUnit: 0,
  targetBg: 5,
  unitAdjustmentPercentage: 0,
  carbsPerColumnWeight: 0,
  columnWeight: 100,
  totalPacketWeight: 100,
  portionRatio: 1,
  minimumCarbs: 0.1,
  lang: 'en'
});

const savedSettings = removeVersion(addUI(JSON.parse(localStorage.getItem('settings') || 'null')) || {});
for (const key in defaultSettings) {
  savedSettings[key] = savedSettings?.[key] ?? defaultSettings[key];
}

