/* eslint-disable react/jsx-no-target-blank */
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { titleChanged } from './store/view';

export const About = () => {
  const dispatch = useDispatch();
  const settings = useSelector(state => state.settings)

  useEffect(() => {
    dispatch(
      titleChanged('About')
    )
  })

  return <section className="proportionatecarbs screen__container">
    <div>
      <p>
        This is a product demo for an upcoming app. Please do not share the link unless given express permission from the author.
      </p>
      <p>
        Author: <a href="https://oliverfoster.co.uk/about" target="_blank">Oliver Foster</a><br />
        Version: {settings.version}<br />
        Update available: {Boolean(settings.needsUpdating) ? "Yes" : "No"}
      </p>
      <p>
        Leave feedback or requests using the form at <a href="https://oliverfoster.co.uk/contact" target="_blank">oliverfoster.co.uk/contact</a>. Make sure to say that your message is regarding the <b>diabetes calculator</b>.
      </p>
      <p>
        Copyright  2021, Oliver Foster<br />
      </p>
    </div>
  </section>

}
