import { configureStore } from '@reduxjs/toolkit'

import settingsReducer from './settings'
import insulinUnitsReducer from './insulinUnits'
import proportionateCarbsReducer from './proportionateCarbs'
import viewReducer from './view'

export default configureStore({
  reducer: {
    view: viewReducer,
    settings: settingsReducer,
    insulinUnits: insulinUnitsReducer,
    proportionateCarbs: proportionateCarbsReducer
  }
})
