export default function round(value, precision) {
  value = Math.round(value / precision) * precision
  const valueAsString = String(value)
  if (valueAsString.includes('.')) {
    const precisionAsString = String(precision)
    const precisionAfterDecimal = precisionAsString.slice(precisionAsString.indexOf('.') + 1)
    const truncatedString = valueAsString.slice(0, valueAsString.indexOf('.') + 1 + precisionAfterDecimal.length)
    return parseFloat(truncatedString)
  }
  return value
}
