import React, { useEffect, useState } from 'react';
import { defaultSettings } from './store/initialState';
import { useDispatch, useSelector } from 'react-redux'
import { TextField, InputAdornment, Button, InputLabel, Select } from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';
import { titleChanged } from './store/view';
import { allSettingsChanged } from './store/settings'
import makeOnChanged from './utils/makeOnChanged';
import makeOnBlur from './utils/makeOnBlur'
import {
  // LocalHospitalOutlined as LocalHospitalOutlinedIcon,
  // Restaurant as RestaurantIcon,
  Cake as CakeIcon,
  Maximize as MaximizeIcon,
  ShowChart as ShowChartIcon,
  FitnessCenter as FitnessCenterIcon,
  Settings as SettingsIcon,
  RadioButtonUnchecked as RadioButtonUncheckedIcon,
  DataUsage as DataUsageIcon,
  Info as InfoIcon
} from '@material-ui/icons'
import { langs } from './lang/langs';

export const Settings = () => {
  const settings = useSelector(state => state.settings)
  const lang = langs[settings.lang]
  const dispatch = useDispatch()
  const onChanged = makeOnChanged(settings, defaultSettings, dispatch, allSettingsChanged, { noui: false })
  const onBlur = makeOnBlur(defaultSettings, dispatch, allSettingsChanged, { noui: false })

  useEffect(() => {
    dispatch(
      titleChanged('Settings')
    )
  })

  const [showPortionRatio, setShowPortionRatio] = useState(false)

  return (
    <section className="settings screen__container">

      <div className="settings_input input__group">

        <div className="settings__input__currentbg input__container">
          <TextField label="Default current blood glucose"
            onChange={onChanged.bind(this, 'currentBg')}
            onBlur={onBlur.bind(this, 'currentBg')}
            value={settings.ui_currentBg}
            InputProps={{
              startAdornment: <InputAdornment position="start"><ShowChartIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">mmol/l</InputAdornment>,
            }}
            helperText=":: This should match default target blood glucose ::"
          />
        </div>

        <div className="settings__input__targetbg input__container">
          <TextField label="Default target blood glucose"
            onChange={onChanged.bind(this, 'targetBg')}
            onBlur={onBlur.bind(this, 'targetBg')}
            value={settings.ui_targetBg}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">mmol/l</InputAdornment>,
            }}
            helperText=":: This should match default current blood glucose ::"
          />
        </div>

        <div className="settings__input__unitadjustmentpercentage input__container">
          <TextField label="Default insulin efficiency adjustment"
            onChange={onChanged.bind(this, 'unitAdjustmentPercentage')}
            onBlur={onBlur.bind(this, 'unitAdjustmentPercentage')}
            value={settings.ui_unitAdjustmentPercentage}
            InputProps={{
              startAdornment: <InputAdornment position="start"><FitnessCenterIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">%</InputAdornment>,
            }}
            helperText=":: Example: +20 for stress/illness/sitting, -20 for exercise ::"
          />
        </div>

        <div className="settings__input__carbsperunit input__container">
          <TextField label="Default carbs per unit"
            onChange={onChanged.bind(this, 'carbsPerUnit')}
            onBlur={onBlur.bind(this, 'carbsPerUnit')}
            value={settings.ui_carbsPerUnit}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
            helperText=":: How many carbs would you need exactly 1 unit to correct? Bigger amounts mean less insulin, as you can eat more carbs per unit. Smaller amounts mean more insulin as you can eat fewer carbs per unit. ::"
          />
        </div>

        <div className="settings__input__bgperunit input__container">
          <TextField label="Default blood glucose correction per unit"
            onChange={onChanged.bind(this, 'bgPerUnit')}
            onBlur={onBlur.bind(this, 'bgPerUnit')}
            value={settings.ui_bgPerUnit}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">mmol/l</InputAdornment>,
            }}
            helperText=":: How many mmol/l would your glucose drop for 1 unit? Bigger amounts mean less insulin as 1 unit will move your blood sugar by a larger degree. Smaller amounts mean more insulin. ::"
          />
        </div>

        <div className="settings__input__columnweight input__container">
          <TextField label="Default per weight"
            onChange={onChanged.bind(this, 'columnWeight')}
            onBlur={onBlur.bind(this, 'columnWeight')}
            value={settings.ui_columnWeight}
            InputProps={{
              startAdornment: <InputAdornment position="start"><MaximizeIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
            helperText=":: The 'per weight' or 'column weight', normally 100g ::"
          />
        </div>

        <div className="settings__input__totalpacketweight input__container">
          <TextField label="Default total (net) weight"
            onChange={onChanged.bind(this, 'totalPacketWeight')}
            onBlur={onBlur.bind(this, 'totalPacketWeight')}
            value={settings.ui_totalPacketWeight}
            InputProps={{
              startAdornment: <InputAdornment position="start"><CakeIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
            helperText=":: Total weight of the packet/cake/meal ::"
          />
        </div>

        <div className="proportionatecarbs__input__portionratio input__container">
          <InputLabel htmlFor="portionratio">{lang.proportionateCarbsPortionRatioLabelText}</InputLabel>
          <div className="select__container">
          <Select
            native
            value={settings.ui_portionRatio}
            onChange={onChanged.bind(this, 'portionRatio')}
            onBlur={onBlur.bind(this, 'portionRatio')}
            label={<div>
            {lang.proportionateCarbsPortionRatioLabelText}
            {lang.proportionateCarbsPortionRatioInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowPortionRatio(!showPortionRatio)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showPortionRatio ? 'flex':'none'}} onClose={() => setShowPortionRatio(false)}>
                {lang.proportionateCarbsPortionRatioInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            inputProps={{
              name: 'portionratio',
              id: 'portionratio',
            }}
            startAdornment={<InputAdornment position="start"><DataUsageIcon /></InputAdornment>}
          >
            <option aria-label="None" value={1}>Whole</option>
            <option value={1/2}>1/2 Half</option>
            <option value={1/3}>1/3 One third</option>
            <option value={1/4}>1/4 One quarter</option>
            <option value={1/5}>1/5 One fifth</option>
            <option value={1/6}>1/6 One sixth</option>
            <option value={1/8}>1/8 One eighth</option>
            <option value={2}>2 Two</option>
            <option value={2/3}>2/3 Two thirds</option>
            <option value={2/5}>2/5 Two fifths</option>
            <option value={3}>3 Three</option>
            <option value={3/5}>3/5 Three fifths</option>
            <option value={3/4}>3/4 Three quarters</option>
            <option value={4}>4 Four</option>
            <option value={4/5}>4/5 Four fifths</option>
            <option value={5}>5 Five</option>
            <option value={5/6}>5/6 Five sixths</option>
          </Select>
          </div>
        </div>

        <div className="settings__input__minimumcarbs input__container">
          <TextField label="Minimum carbs"
            onChange={onChanged.bind(this, 'minimumCarbs')}
            onBlur={onBlur.bind(this, 'minimumCarbs')}
            value={settings.ui_minimumCarbs}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
            helperText=":: Carbs will be rounded to this amount ::"
          />
        </div>

        <div className="settings__input__minimumunits input__container">
          <TextField label="Minimum units"
            onChange={onChanged.bind(this, 'minimumUnits')}
            onBlur={onBlur.bind(this, 'minimumUnits')}
            value={settings.ui_minimumUnits}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">unit</InputAdornment>,
            }}
            helperText=":: Units will be rounded to this amount ::"
          />
        </div>


      </div>

    </section>
  )
}
